	import {uniCollapse,uniCollapseItem} from "@dcloudio/uni-ui"
	export default {
		data() {
			return {
				array: ['中国', '美国', '巴西', '日本'],
                index: "",
				selectValue:"", 
				imageList:[],
				sketch:"",
				pathArr:[],
				listPath:[],
				percent:0,
				showBox:false,
				state:false,
			}
		},
		methods: {
			/**
			 * 删除数组图片
			 */
			deletePic:function(e){
				var _self = this
				console.log(e)
				_self.imageList.splice(e,1)
				_self.pathArr.splice(e,1)
			},
			bindPickerChange: function(e) {
            console.log('picker发送选择改变，携带值为', e.target.value)
            this.index = e.target.value
			},
			previewImage: function(e) {
				var current = e.target.dataset.src
				uni.previewImage({
					current: current,
					urls: this.imageList
				})
			},
			chooseImage: async function() {
				var _self = this
					if (this.imageList.length === 9) {
						let isContinue = await this.isFullImg();
						console.log("是否继续?", isContinue);
						if (!isContinue) {
							return;
						}
					}
					uni.chooseImage({
						success: (res) => {
							console.log(res)
							console.log(JSON.stringify(res.tempFilePaths));
							var tempFilePaths = res.tempFilePaths;
							this.imageList = this.imageList.concat(res.tempFilePaths);
							for(var i =0;i<tempFilePaths.length;i++){
							 uni.uploadFile({
								url: _self.$api+'dockingManager/upload', //仅为示例，非真实的接口地址
								//url: "http://www.tp5.com/index", //仅为示例，非真实的接口地址
								filePath: tempFilePaths[i],
								name: 'file', 
								formData: {
									'user': 'test'
								},
								success: (uploadFileRes) => {
									_self.pathArr = _self.pathArr.concat(JSON.parse(uploadFileRes.data))
								}
							  }); 
							}
							_self.state = true
						}	
					})
				},
				isFullImg: function() {
					return new Promise((res) => {
						uni.showModal({
							content: "已经有9张图片了,是否清空现有图片？",
							success: (e) => {
								if (e.confirm) {
									this.imageList = [];
									res(true);
								} else {
									res(false)
								}
							},
							fail: () => {
								res(false)
							}
						})
					})
				},
			showOrHide:function(){
				this.showBox = false
			},
			addAdvice:function(){
				var _self = this
				if(_self.sketch == ""){
					uni.showToast({
						title:"请填写您的建议",
						icon:"none"
					})
					return false
				}
				// if(_self.percent != 100 && _self.imageList.length>0){
				// 	uni.showToast({
				// 		title:"图片正在上传，请稍后提交",
				// 		icon:"none"
				// 	})
				// 	return false
				// }
				if(_self.state == false && _self.imageList.length>0){ 
				uni.showToast({
					title:"图片正在上传，请稍后提交",
					icon:"none"  
				})
				return false
				}
				for(var i =0;i<_self.pathArr.length;i++){
					_self.listPath.push({testName1:_self.pathArr[i]})
					console.log(_self.listPath)
				}
				_self.listPath.push({sketch:_self.sketch,optionId:uni.getStorageSync("openId")})
				var _self = this
				uni.request({
					url:_self.$api+"dockingManager/proposalAdd",
					data:JSON.stringify(_self.listPath),
					method:"POST",
					success:function(res){
						if(res.data ==99){
								uni.showModal({
									title: '提示',
									content: '此操作需用户授权，是否进行授权',
									success: function (res) {
										if (res.confirm) {
											//跳转到授权页面  
											uni.navigateTo({
												url:"../../login/login"  
											})
											console.log('用户点击确定');
										} else if (res.cancel) {
											console.log('用户点击取消');
										}
									}
								});
								return false
							
						}else if(res.data ==0){
								uni.showToast({
								title:"发布失败",
								icon:"none"
						})
							return false
						}else if(res.data ==1){
								uni.showToast({
								title:"提交成功",
								duration:2000,
								success:function(){
									setTimeout(function () {
										  //要延时执行的代码
										uni.switchTab({
											url:"../../../pages/index/index"
										})
										}, 1000) //延迟时间
								}
							})
							return false
						}else{
							uni.showToast({
								title:"系统异常"
							})
							return false
						}
					}
					
				})
			}
		},
		 components: {uniCollapse,uniCollapseItem}
	}
